.notes__main-content{
    display: flex;
    flex-direction: column;
    height: 100%;
}
.notes__appbar{
    align-items: center;
    background-color: $primary;
    color: white;
    display: flex;
    justify-content: space-between;
    padding: 10px 20px 10px 20px;
    font-size: 20px;
}
.notes__content{
    display: flex;
    flex-direction: column;
    height: 100%;
    padding: 20px;
}
.notes__title-input, .notes__textarea{
    border: none;
    &:focus{
        outline: none;
    }

}
.notes__title-input{
    color: $dark-grey;
    font-size: 25px;
    margin-bottom: 10px;
    font-weight: 700;
}
.notes__textarea{
    border: none;
    color: $dark-grey;
    flex: 1 1 auto;//para que se extienda
    font-size: 20px;
    resize: none;//quita el icono de la izquierda inferior

}
.notes__image img{
    box-shadow: 5px 5px $dark-grey;
    max-height: 200px;
}